.certs {
  max-width: 1200px;
  margin: auto;
  padding-bottom: 10px;
  padding: 20px;

  &-item {
    display: flex;
    margin-bottom: 50px;

    &-text {
      align-self: flex-start;
      justify-self: center;
      text-align: end;
      margin-right: 20px;
      padding: 20px 10px;
      border-right: 1px solid $grey-36;
      
      &-desc {
        font-family: $font-rob;
      }
    }
    
    &-img {
      width: 45vw;
    }

    &-normal { justify-content: flex-end; }

    &-reverse {
      flex-direction: row-reverse;

      &-text {
        text-align: start;
        border-right: 0px;
        border-left: 1px solid $grey-36;
        margin-left: 20px;
      }
    }
  } 
}

@media only screen and (max-width: 650px) {
  .certs-item-text { font-size: 10px; }
}